.questions {
  padding: 93px 0 109px;
  background:  url("../img/questions-background.svg") no-repeat 98% -60px,
    $accent-color;
  font-family: $main-font-family;
  color: $font-color-light;
  font-size: 16px;
  line-height: 22px;

  @media (max-width: $tablet-width) {
    padding: 80px 0 103px;
    background: $accent-color;
  }

  @media (max-width: $mobile-width) {
    padding: 30px 0 42px;
  }
}

.questions__narrow-container {
  display: flex;
}

.questions__wrapper {
  position: relative;
  margin-left: auto;
  width: 49%;

  &::before {
    position: absolute;
    content: "";
    width: 511px;
    height: 288px;
    top: 68px;
    left: -625px;
    background: url("../img/paper-airplane.png") no-repeat center;
  }

  @media (max-width: $tablet-width) {
    margin-left: 0;
    padding: 0 63px;
    width: 100%;

    &::before {
      @include visually-hidden;
    }
  }

  @media (max-width: $mobile-width) {
    padding: 0;
  }
}

.questions__title {
  @include page-title;
  margin-bottom: 8px;
  font-size: 36px;
  line-height: 51px;

  @media (max-width: $tablet-width) {
    margin-bottom: 16px;
    font-size: 32px;
    line-height: 45px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 13px;
    font-size: 28px;
    line-height: 39px;
  }
}

.questions__info {
  margin-bottom: 51px;

  @media (max-width: $tablet-width) {
    margin-left: 5px;
    margin-bottom: 53px;
  }

  @media (max-width: $mobile-width) {
    margin-left: 0;
    margin-bottom: 26px;
  }
}

.questions__form {
  @media (max-width: $tablet-width) {
    margin-left: 4px;
  }

  @media (max-width: $mobile-width) {
    margin-left: 0;
  }
}

.questions__agreement {
  margin-top: 8px;
}
