.site-form {
  font-family: $main-font-family;
  color: $font-color-light;
  font-size: 16px;
  line-height: 22px;
}

.site-form__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @media (max-width: $mobile-width) {
    flex-direction: column;
    justify-content: flex-start;
  }

  &--submit {
    @media (max-width: $tablet-width) {
      justify-content: flex-start;
    }
  }
}

.site-form input,
.site-form textarea,
.site-form select {
  appearance: none;
}

.site-form input,
.site-form textarea {
  margin-bottom: 40px;
  box-sizing: border-box;
  padding: 14px 16px;
  border: 1px solid transparent;
  background: #19baff;
  font-size: 16px;
  line-height: 22px;
  font-family: $main-font-family;
  color: $font-color-light;
  transition: 0.3s;

  &:hover,
  &:focus {
    outline: none;
    border: 1px solid #ffffff;
  }

  &:invalid {
    outline: none;
    box-shadow: none;
    border: 1px solid #ff5660;
  }

  @media (max-width: $tablet-width) {
    padding: 15px 18px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 31px;
    padding: 13px 18px 16px 17px;
  }
}

.site-form input::placeholder,
.site-form textarea::placeholder {
  color: $font-color-light;
}

.site-form input {
  width: 47.4%;

  @media (max-width: $mobile-width) {
    width: 100%;
  }
}

.site-form textarea {
  width: 100%;
  min-height: 154px;
  resize: none;

  @media (max-width: $tablet-width) {
    min-height: 161px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 24px;
    min-height: 104px;
  }
}

.site-form input[type="checkbox"] {
  @include visually-hidden;
}

.site-form input[type="checkbox"] + label {
  position: relative;
  cursor: pointer;

  &::before {
    position: absolute;
    content: "";
    width: 14px;
    height: 14px;
    top: 1px;
    left: -32px;
    border: 1px solid #ffffff;
  }

  @media (max-width: $mobile-width) {
    margin-left: 34px;
  }
}

.site-form input[type="checkbox"]:checked + label {
  &::before {
    background: url("../img/icons/checkbox-icon.svg") no-repeat center;
  }
}

.site-form button[type="submit"] {
  margin-right: 70px;
  min-width: 270px;

  @media (max-width: $tablet-width) {
    margin-right: 73px;
    min-width: 280px;
  }

  @media (max-width: $mobile-width) {
    margin-right: 0;
    margin-bottom: 23px;
    width: 100%;
    min-width: 100%;
  }
}

.site-form__agreement {
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8);
}
